import React from 'react'
import './Filter.sass'
import { connect } from 'react-redux'
import { Button } from '@material-ui/core'
import { Done } from '@material-ui/icons'
import {
  resetFilter,
  getFilters,
  addFilter,
  removeFilter,
} from 'redux/actions/products'
import FilterBlock from './FilterBlock/FilterBlock'


const Filter = ({
  searched,
  hide,
  resetFilter,
  addFilter,
  removeFilter,
  globalFilter
}) => {
  const [byColor, bySize] = getFilters(searched, ['color', 'size'])

  return (
    <div className={`Filter ${hide ? 'hide' : ''}`}>
      <FilterBlock
        label='Color'
        globalFilterType='color'
        currentFilter={byColor}
        itemsContent={<Done fontSize='small' color='primary' />}
        classNameAsValue
        className='color'
        addFilter={addFilter}
        removeFilter={removeFilter}
        globalFilter={globalFilter}
      />
      <FilterBlock
        label='Size'
        globalFilterType='size'
        currentFilter={bySize}
        className='size'
        addFilter={addFilter}
        removeFilter={removeFilter}
        globalFilter={globalFilter}
      />
      <Button
        variant='contained'
        className='Filter-reset'
        onClick={resetFilter}
      >
        Reset
      </Button>
    </div>
  )
}

const mapStateToProps = state => ({
  searched: state.products.searched,
  globalFilter: state.products.filter
})
const mapDispatchToProps = dispatch => ({
  resetFilter: () => dispatch(resetFilter()),
  addFilter: value => dispatch(addFilter(value)),
  removeFilter: value => dispatch(removeFilter(value)),
})

export default connect(mapStateToProps, mapDispatchToProps)(Filter)