import React from 'react'
import { Button, Typography, withStyles } from '@material-ui/core'
import cx from 'classnames';


const FilterButton = withStyles(() => ({
  root: {
    minWidth: 30
  }
}))(Button)

const FilterBlock = ({
  globalFilter,
  globalFilterType,
  currentFilter,
  label,
  itemsContent,
  classNameAsValue,
  className,
  addFilter,
  removeFilter
}) => {
  const setActive = (filter, value) => filter.indexOf(value) > -1
  const filterItems = currentFilter.map(filterItem => {
    const active = setActive(globalFilter[globalFilterType], filterItem)
    const handleFilter = () => {
      active ?
      removeFilter({
        filter: globalFilterType,
        value: filterItem
      }) :
      addFilter({
        filter: globalFilterType,
        value: filterItem
      })
    }

    return (
      <FilterButton
        key={filterItem}
        className={cx({
          [className]: className,
          [filterItem]: classNameAsValue,
          active
        })}
        onClick={handleFilter}
        children={itemsContent ? itemsContent : filterItem}
      />
    )
  })
  return (
    <div className='FilterBlock'>
      <Typography variant='h6' className='FilterBlock-header'>
        {label}
      </Typography>
      <div className='FilterBlock-content'>
        {filterItems}
      </div>
    </div>
  )
}

export default FilterBlock
